%h1{
  font-family: Poppins, sans-serif;
  font-size: 52px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  letter-spacing: -2px;
}
%h2{
  font-family: Poppins, sans-serif;
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  color: $main-text-color;
}
%h3{
  font-family: Poppins, sans-serif;
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 130%;
  color: $main-text-color;
}
%h4{
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  color: $main-text-color;
}
%p{
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  color: $subtitle-color;
}
